.corny {
  display: block;
  padding: .5em;
  font-size: .8rem;
  color: white;
  text-align: center;
  background-color: red;
  -webkit-transition: opacity .5sec ease;
  -o-transition: opacity .5sec ease;
  transition: opacity .5sec ease;
  position: absolute;
  top: 0;
  right: 0;
  text-decoration: none;
}
.corny:hover {
  opacity: 1 !important;
  -webkit-box-shadow: -5px 5px 10px #ffa500;
          box-shadow: -5px 5px 10px #ffa500;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  height: 100%;
}
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
header {
  overflow: hidden;
}
h1 {
  font-size: 2em;
  line-height: 2rem;
  margin: 1rem 0;
}
label,
button,
input,
.button {
  cursor: pointer;
}
#siteWrap {
  height: 100%;
  position: relative;
}
.hide {
  display: none !important;
}
.mainColumn {
  width: 90%;
  max-width: 52rem;
  margin: 0 auto;
}
.mainSection {
  margin-bottom: 1em;
}
.section-title {
  margin-top: .2em;
  margin-bottom: .5em;
}
.section-sub-title {
  margin-top: .2em;
  margin-bottom: .5em;
  line-height: 1;
}
.settings-section {
  margin: 0 0 1em;
}
.button {
  padding: .3rem .8rem;
  margin: 0;
  font-family: 'Comic Neue', Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 1rem;
  color: #000000;
  background: none #e6e6e6;
  -webkit-appearance: none;
  outline: none;
  border: solid 1px;
}
.button:hover,
.button.hover {
  background-color: #ffffff;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 100, 255, 0.6);
          box-shadow: inset 0 0 5px rgba(0, 100, 255, 0.6);
}
.button:active,
.button.active {
  -webkit-box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.8);
          box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.8);
}
.button:focus,
.button.focus {
  background-color: #ffffff;
}
.button[disabled] {
  opacity: 0.3;
}
.text-small {
  font-size: .6rem;
}
.inline-input {
  padding: 0 .5rem;
  border-radius: .3em;
}
.input-simple {
  max-width: 3rem;
}
.input-simple::-webkit-inner-spin-button, 
.input-simple::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
#pageUI .inline-input .text-small {
  font-weight: bold;
}

.button-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}
  .button-list-item {
    display: inline-block;
  }
  .button-list-item.button,
  .button-list-item .input {
    line-height: 1rem;
  }
  /*.button-list label {
    text-align: right;
  }
  .button-list label + input {
    text-align: left;
  }*/

.problems {
  margin: 0;
  padding: 0 0 5rem;
  list-style: none outside none;
}
.prob {
  display: inline-block;
  min-width: 2rem;
  width: 6%;
  margin: 1rem 1.9%;
  padding: 0;
  font-weight: bold;
  overflow: hidden;
}
.question {
  display: block;
}
.question span {
  display: inline-block;
}
.question .num-a,
.question .num-b {
  display: block;
  text-align: right;
}
.question .operator {
  position: absolute;
}
.question .equals {
  display: block;
  text-indent: -9999px;
  height: 0;
  border-bottom: solid 1px;
}
.answer {
  display: block;
  width: 100%;
  margin: 0;
  padding: .2em 0;
  font-size: 1em;
  text-align: right;
  border: 0 none;
}
.answer[data-solve*='0'],
.answer[data-solve*='1'],
.answer[data-solve*='2'],
.answer[data-solve*='3'],
.answer[data-solve*='4'],
.answer[data-solve*='5'],
.answer[data-solve*='6'],
.answer[data-solve*='7'],
.answer[data-solve*='8'],
.answer[data-solve*='9'] {
  background-color: rgba(0, 170, 255, 0.1);
}
.answer.correct {
  background-color: rgba(0, 255, 0, 0.3);
}
.answer.incorrect {
  background-color: rgba(255, 0, 0, 0.3);
}
.answer.incorrect-fixed {
  background-color: rgba(255, 255, 0, 0.5);
}
/*
	SETTINGS
*/
.settings {
  width: 300px;
  margin: 0;
  padding: 1em;
  color: white;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
  background-color: #808080;
  position: absolute;
  top: 0;
  bottom: 0;
  right: -300px;
  z-index: 100;
  -webkit-transition: margin 1s ease;
  -o-transition: margin 1s ease;
  transition: margin 1s ease;
}
.settings.open {
  margin-right: 300px;
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
}
.settings .settings-menu-toggle {
  width: 2em;
  height: 1.5em;
  padding: 0;
  margin: 0;
  font-size: 1.5rem;
  line-height: 1.5em;
  text-align: center;
  opacity: 1;
  position: absolute;
  top: 1em;
  left: -2em;
}
.open.settings .settings-menu-toggle {
  opacity: .3;
}
.settings-list {
  display: block;
  list-style: none inside none;
  margin: 0;
  padding: 0;
}
/*
	Settings operator button list
*/
.button-list-grid {
  width: 100%;
  margin: 0;
  padding: 0;
}
.button-list-grid .button-grid-item {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  margin: 0;
  padding: 0;
  border-style: solid;
  border-width: 1px 1px 1px 0;
}
.button-list-grid .button-grid-item:first-child {
  border-width: 1px;
}
.button-list-grid .button {
  display: block;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 1.7999999999999998rem;
  line-height: 3rem;
  text-align: center;
  border: none;
}
.button-list-grid .button-grid-input {
  display: none;
}
.button-list-grid .button-grid-input:checked + .button,
.button-list-grid .button-grid-input.selected + .button,
.button-list-grid .button.selected {
  background-color: #3d5eff;
  color: white;
}
.button-list-grid.input-radio .button-grid-item {
  margin: 0 1em 0 0;
  border-radius: 1.5rem;
  overflow: hidden;
  background-clip: border-box;
}
.button-list-grid.input-radio .button-grid-item .button {
  border-radius: 1.5rem;
}
/* Radio button driven sections */
.radio-section .settings-list-item {
  margin-bottom: 1em;
}
.radio-section-label {
  text-shadow: none;
}
.radio-section-item {
  margin: .25em 0 0 1em;
  opacity: .1;
}
.radio-section-item-input {
  width: 4rem;
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  background: none transparent;
  -webkit-appearance: none !important;
  font-size: 1.5rem;
  line-height: normal;
  text-align: center;
  color: white;
  font-weight: normal;
  text-shadow-color: blue;
  border-bottom: dashed 1px;
}
.radio-section-input {
  display: none;
}
.settings-sub-subtitle.button.selected {
  background-color: #3d5eff;
  color: white;
}
.settings-sub-subtitle.button.selected + .radio-section-item,
.settings-sub-subtitle.button.selected + .radio-section-item + .radio-section-item {
  opacity: 1;
}
.error-message {
  padding: .2rem;
  visibility: hidden;
  font-size: .6rem;
  color: red;
  font-style: italic;
  text-shadow: none;
}

/* Modal popup */
.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
}
.modal.open {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.modal-overlay {
  background: none rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;

  opacity: 0;
  -webkit-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
  will-change: contents;
}
.modal-box {
  width: 50%;
  min-width: 300px;
  background-color: white;
  text-align: center;
  border-radius: 1em;
  -webkit-box-shadow: 5px 5px 10px black;
          box-shadow: 5px 5px 10px black;
  position: relative;
  z-index: 101;

  opacity: 0;
  -webkit-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
}
.modal-overlay,
.modal-box {
  
}
.modal.open .modal-overlay,
.modal.open .modal-box {
  opacity: 1;
}
.modal-answer .answer-total {
  font-weight: bold;
}



